(function (d, w) {
  const doc = d.documentElement
  function rem () {
    const width = Math.min(doc.getBoundingClientRect().width, 768)
    if (!width) return
    doc.style.fontSize = 100 * (width / 750) + 'px'
  }
  rem()
  w.addEventListener('resize', rem)
})(document, window)



