<template>
    <div id="caihui">
      <div id="loadingDiv" v-show="loadingDivShow">
        <div class="curtain"></div>
        <p class="loadingP" v-show="loadingShow">加载中..</p>
        <img v-show="!loadingShow" class="touchTips" src="./img/touchTips.png"/>
      </div>
      <guaguaka v-if="guaguakaShow" ref="guaguaka" :guaguakaConfig="guaguakaConfig" id="caihuiDiv" @success="success" @hideLoading="hideLoading"></guaguaka>
      <div id="animation" v-if="animationShow"></div>
      <div id="endDiv" v-show="endDivShow">
        <img src="./img/engImg.png">
      </div>
    </div>
</template>

<script>
import $ from 'jquery'
import guaguaka from '@/views/myPlugin'
import prizeImg from './img/prizeImg.png'
import curtainImg from './img/curtainImg.png'
import touchImg from './img/touchImg.png'
import luckDrawMp3 from './audio/luckDraw.mp3'
import audioPlayer from '@/utils/audio.js'
export default {
  name: 'index',
  components: {
    guaguaka
  },
  data () {
    return {
      windowWidth: document.documentElement.style.fontSize.split('px')[0],
      guaguakaConfig: {
        id: 'caihuiDiv',
        width: '7.5rem',
        height: '12.6rem',
        visibleArea: 70,
        curtainImg: curtainImg,
        curtainHide: true,
        prizeImg: prizeImg,
        isTouch: false,
        touchSpotShow: true,
        touchSpotImg: touchImg,
        touchSpotSize: '0.25rem',
        touchSpotImgWidth: '1.16rem',
        touchSpotImgHeight: '1.56rem',
        touchSpotPosition: '0.26 0.15'
      },
      luckDrawMp3: new audioPlayer.SoundComponent({src: luckDrawMp3, altSrc: luckDrawMp3}),
      loadingDivShow: true,
      loadingShow: true,
      guaguakaShow: false,
      animationShow: false,
      endDivShow: false
    }
  },
  mounted () {
    const that = this
    $('#loadingDiv').on('click', function () {
      if (!that.loadingShow) {
        that.luckDrawMp3.play()
        that.luckDrawMp3.stop()
        $('#loadingDiv').attr('class', 'hide')
        setTimeout(function () {
          // that.luckDrawMp3.play()
          that.loadingDivShow = false
          that.$refs.guaguaka.setInfo({isTouch: true})
          // setTimeout(function () {
          //   that.$refs.guaguaka.setInfo({isTouch: true})
          // }, 2000)
        }, 2100)
      }
    })
    this.guaguakaConfig.width = window.innerHeight < 12.06 * this.windowWidth ? (window.innerHeight / this.windowWidth / 12.06 * 7.5) + 'rem' : '7.5rem'
    this.guaguakaConfig.height = window.innerHeight < 12.06 * this.windowWidth ? (window.innerHeight / this.windowWidth) + 'rem' : '12.06rem'
    this.guaguakaShow = true
  },
  methods: {
    hideLoading () {
      this.loadingShow = false
    },
    success (id) {
      console.log(id)
      const that = this
      setTimeout(function () {
        that.setAnimation()
        $('#caihuiDiv').attr('class', 'hide1')
        setTimeout(function () {
          that.guaguakaShow = false
        }, 6500)
        setTimeout(function () {
          that.animationShow = false
          that.endDivShow = true
        }, 7000)
      }, 1500)
    },
    setAnimation () {
      this.animationShow = true
      let animationPosition = 0
      const mysetInterval = setInterval(function () {
        animationPosition = animationPosition - 7.5
        console.log(animationPosition)
        if (animationPosition < -300) {
          animationPosition = -292.5
          clearInterval(mysetInterval)
        }
        $('#animation').css({
          backgroundPosition: animationPosition + 'rem 0'
        })
      }, 150)
    }
  }
}
</script>

<style scoped lang="scss">
  #caihui{
    p{
      margin: 0;
    }
    position: absolute;
    height: 100%;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    #loadingDiv{
      position: fixed;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      z-index: 200;
      display: flex;
      align-items: center;
      justify-content: center;
      .curtain{
        position: absolute;
        opacity: 0.8;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        background-color: black;
      }
      .loadingP{
        position: absolute;
        color: white;
        text-align: center;
      }
     .touchTips{
       position: absolute;
       width: 3.64rem;
       height: 3.07rem;
     }
    }
    #caihuiDiv{
      margin: auto;
    }
    #animation{
      position: absolute;
      width: 7.5rem;
      height: 12.06rem;
      background-image: url("./img/hua1.png");
      background-size: 300rem 12.06rem;
      background-repeat: no-repeat;
      z-index: 200;
    }
    #endDiv{
      position: absolute;
      font-size: 0;
      img{
        width: 7.5rem;
      }
    }
    .hide{
      animation: hide 1.5s;
      animation-fill-mode: forwards;
    }
    .hide1{
      animation: hide 6.5s;
      animation-fill-mode: forwards;
    }
    @keyframes hide {
      0% {opacity: 1;}
      10% {opacity: 0.9;}
      20% {opacity: 0.8;}
      30% {opacity: 0.7;}
      40% {opacity: 0.6;}
      50% {opacity: 0.5;}
      60% {opacity: 0.4;}
      70% {opacity: 0.3;}
      80% {opacity: 0.2;}
      90% {opacity: 0.1;}
      100% {opacity: 0;}
    }
  }
</style>



